.intro-img-wp {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  max-height: 100%;
  content: "";

  .intro-img {
    width: 100%;
    height: 100%;
    max-width: 1200px;
    max-height: 900px;
    content: "";
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    filter: blur(8px);
    -webkit-filter: blur(8px);
    transition: all 2s ease-in-out;
  }
}

.landing-intro {
  &.in {
    .intro-title {
      .intro-img-wp {
        .intro-img {
          filter: blur(0px);
          -webkit-filter: blur(0px);
        }
      }
    }
  }
}