$introTextShadow: 1px -1px 1px rgba(0,0,0,.1), 1px 1px 1px rgba(0,0,0,.4);

.landing-intro-back {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9997;
  background: rgba(0, 0, 0, 0.5);
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.7) 0%, transparent 70%, transparent 100%);
  mix-blend-mode: color-burn;
}

.landing-intro {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  z-index: 9998;
  padding: 20px;

  &.menu-left {
    padding-left: 70px;
  }

  &.menu-bottom {
    padding-bottom: 70px;
  }

  .intro-title, .intro-slide {
    align-self: stretch;
    position: relative;
    transition: all 1s ease-in-out;
  }

  .intro-title {
    padding-bottom: 5vh;

    .intro-img-wp {
      flex: 1;
      padding: 5vh 0;
    }

    @media screen and (max-height: 400px) {
      padding-bottom: 2vh;

      .intro-img-wp {
        padding: 0 0 2vh 0;
      }
    }
  }

  &.phone {
    top: 0;
    bottom: 0;
    height: auto;

    &.portrait {
      .intro-title {
        padding-bottom: 0;
        p {
          font-size: 1.6rem;
        }
      }
    }

    &.landscape {
      padding-bottom: 40px;
      .intro-title {
        // padding-bottom: 20px;
        p {
          font-size: 2.5rem;
        }
      }
    }
  }

  .intro-title {
    flex: 1;
    display: flex;
    flex-direction: column;

    .intro-btn {
      width: 100%;
      max-width: 200px;
      cursor: pointer;
      margin: auto;

      svg * {
        cursor: pointer;
      }

      @media screen and (max-height: 400px) {
        max-width: 150px;
      }
    }

    p {
      color: white;
      margin: 0;
      font-size: 3rem;
      text-align: center;
      text-shadow: $introTextShadow;
      font-weight: 200;

      @media screen and (min-width: 1400px) {
        font-size: 5rem;
      }

      @media screen and (max-width: 767px) {
        font-size: 2rem;
      }
    }
  }

  .intro-slide {
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  &.out {
    .intro-title {
      opacity: 0;
      bottom: 50px;
    }
    .intro-slide {
      opacity: 0;
      top: 50px;
    }
  }

  &.in {
    .intro-title {
      opacity: 1;
      bottom: 0;
    }
    .intro-slide {
      opacity: 1;
      top: 0;
    }
  }
}
