$hsWidth: 60px;
$hsRatio: $hsWidth / 60px;
$anim-height: $hsWidth * 1.4;
$anim-steps: 40;
$anim-width: $anim-height * $anim-steps;

.scene-hotspot {
  width: $hsWidth;
  height: $hsWidth;
  position: relative;
  cursor: pointer;
  // pointer-events: none;
  // transform: translate(50%, -50%);
  &.z-index-99 {
    z-index: 99;
  }

   > .s-animation {
    position: absolute;
    height: $hsWidth * 1;
    width: $hsWidth * 1;
    top: $hsWidth * 0;
    left: $hsWidth * 0;
    pointer-events: none;

    &.position-unset {
      position: unset;
    }

    > .s-animation-circle {
      position: absolute;
      pointer-events: none;
      height: $anim-height;
      width: $anim-height;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 50%;
      &.animating {
        background-image: url('./hs_anim_40.png');
        background-size: $anim-width $anim-height;
        -webkit-animation: circling 2s steps($anim-steps) infinite;
        -moz-animation: circling 2s steps($anim-steps) infinite;
        -ms-animation: circling 2s steps($anim-steps) infinite;
        -o-animation: circling 2s steps($anim-steps) infinite;
        animation: circling 2s steps($anim-steps) infinite;
      }
    }
  }

  > .s-info {
    position: absolute;
    height: $hsWidth;
    width: $hsWidth / 2;
    top: 0;
    left: 0;
    z-index: 2;
    pointer-events: none;
    opacity: 0;
    transition: none;

    .s-info-title,
    .s-info-distance {
      position: absolute;
      left: 100%;
      padding-left: 42px * $hsRatio;

      &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        background-repeat: no-repeat;
        background-position: right top;
        background-size: auto 100%;
        width: 42px * $hsRatio;
        height: 100%;
      }

      p {
        white-space: nowrap;
        margin: 0;
        font-family: var(--hotspot-font);
      }
    }

    .s-info-title {
      top: 0;
      height: $hsWidth / 2;

      &:before {
        top: 0;
        background-image: url('./bg-white.png');
      }

      p {
        line-height: $hsWidth / 2;
        background-color: white;
        color: black;
        font-size: 16px;
        // font-weight: 300;
        text-transform: uppercase;
        padding-right: 12px;
        box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1),
          1px -1px 1px rgba(0, 0, 0, 0.1);
      }
    }

    .s-info-distance {
      bottom: 0;
      height: $hsWidth / 2 - 2px;
      opacity: 0.7;

      &:before {
        bottom: 0;
        background-image: url('./bg-black.png');
      }

      p {
        line-height: $hsWidth / 2 - 2px;
        background-color: black;
        color: white;
        font-size: 14px;
        font-weight: 700;
        padding-right: 10px;
      }
    }
  }

  > .s-icon {
    position: absolute;
    width: $hsWidth;
    height: $hsWidth;
    top: 0;
    left: 0;
    z-index: 3;
  }

  img {
    position: absolute;
    width: $hsWidth;
    height: $hsWidth;
    z-index: 4;
    display: none;
    opacity: 0;
    top: 0;
    left: 0;
    transition: all 0.2s ease-in-out;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    border: 1px solid white;
  }

  > .s-visited {
    position: absolute;
    width: $hsWidth * 0.3;
    height: $hsWidth * 0.3;
    top: $hsWidth * 0.1;
    right: $hsWidth * 0.1;
    z-index: 5;
  }

  &.hovering {
    img {
      opacity: 1;
      display: block;
    }

    > .s-info {
      opacity: 1;
    }

    > .s-visited {
      opacity: 0;
    }
  }
}

@-webkit-keyframes circling {
  from {
    background-position: 0px;
  }
  to {
    background-position: -$anim-width;
  }
}

@-moz-keyframes circling {
  from {
    background-position: 0px;
  }
  to {
    background-position: -$anim-width;
  }
}

@-ms-keyframes circling {
  from {
    background-position: 0px;
  }
  to {
    background-position: -$anim-width;
  }
}

@-o-keyframes circling {
  from {
    background-position: 0px;
  }
  to {
    background-position: -$anim-width;
  }
}

@keyframes circling {
  from {
    background-position: 0px;
  }
  to {
    background-position: -$anim-width;
  }
}
