.intro-slide-container {
  width: 100%;
  max-height: 100%;
  .intro-slide-item-list {
    max-height: 100%;
    display: flex;
    flex-direction: column-reverse;

    .intro-slide-item {
      outline: none !important;
      .intro-slide-item-body {
        padding: 0 20px;

        @media screen and (min-width: 1200px) {
          padding: 0 50px;
        }

        &.pr-1 {
          padding-right: 1px;
        }

        .intro-svg {
          width: 100%;
          position: relative;
  
          .intro-logo {
            width: 70%;
            max-width: 100px;
            margin-left: auto;
            position: absolute;
            bottom: 0;
            right: 0;
          }
  
          svg {
            width: 100%;
          }

          &.ml-auto {
            margin-left: auto;
          }
        }
  
        .intro-text {
          color: rgba(255, 255, 255, 0.5);
          text-transform: uppercase;
          font-family: var(--condensed-font);
          font-size: 0.9rem;
          padding-top: 1rem;
          height: 4rem;
          line-height: 1rem;

          &.text-right {
            text-align: right;
          }

          @media screen and (min-width: 992px) {
            font-size: 0.7rem;
            padding-top: 0.5rem;
            height: 0.9rem * 2 + 0.5rem;
            line-height: 0.9rem;
          }

          @media screen and (min-width: 1400px) {
            font-size: 0.9rem;
            padding-top: 0.9rem;
            height: 0.9rem * 3;
            line-height: 0.9rem;
          }
        }
      }
    }
  }

  &.phone {
    &.landscape {
      .intro-slide-item-list {
        .intro-slide-item {
          .intro-slide-item-body {
            padding-left: 30px;
            padding-right: 30px;
            padding-top: 0;

            .intro-text {
              padding-top: 0.5rem;
              height: 2.5rem;
            }
            .intro-svg {
              max-width: 25vw;
            }
          }
        }
      }
    }
    &.portrait {
      .intro-slide-item-list {
        .intro-slide-item {
          .intro-slide-item-body {
            .intro-svg {
              max-width: 50vw;
            }
          }
        }
      }
    }
  }

  &.desktop {
    .intro-slide-item-list {
      .intro-slide-item {
        .intro-slide-item-body {
          .intro-svg {
            max-width: 15vw;
          }
        }
      }
    }
  }
}

.slick-slider {
  .slick-track {
    display: flex !important;
    .slick-slide {
      height: inherit !important;
      > div {
        outline: none !important;
      }
    }
  }
}

.slick-dots {
  li button:before {
    color: white !important;
    font-size: 10px !important;
    opacity: 0.5 !important;
  }
  li.slick-active button:before {
    color: white !important;
    opacity: 0.9 !important;
  }
}
