.Staging {
  height: 2px;
  width: 2px;
  position: relative;
  .PopOver {
    font-family: var(--secondary-font);
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    width: 250px;
    padding: 2px;
    border-radius: 8px;
    background-color: rgba(255, 255, 255, 0.9);
    transition: opacity ease-in-out 2s;
    .Wrapper {
      padding: 12px;
      border: 1px solid #373737;
      border-radius: 8px;
      &--title {
        margin: 0;
        margin-bottom: 5px;
        font-size: 1.125rem;
        color: #373737;
      }
      &--description {
        margin: 0;
        margin-bottom: 5px;
        font-size: 0.9rem;
        color: #373737;
      }
    }
  }
}
