.loading-spinner {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  padding: 15px;
  transition: background 1s;

  .loading-logo {
    width: 200px;
    transition: opacity 1s;

    img {
      max-width: 100%;
    }

    @media screen and (max-width: 991px) {
      width: 150px;
    }
  }
}
