$hsWidth: 30px;
$hsMaxWidth: 34px;
$mWidth: 200px;

.media-hotspot {
  width: $hsWidth;
  height: $hsWidth;
  position: relative;
  cursor: pointer;
  animation: hpScaleBig 2s ease-in-out 0s infinite normal;
  -webkit-animation: hpScaleBig 2s ease-in-out 0s infinite normal;
  -moz-animation: hpScaleBig 2s ease-in-out 0s infinite normal;

  .m-info {
    display: none;
    position: absolute;
    width: $mWidth;
    top: $hsWidth + 5px;
    left: $hsWidth / 2;
    padding: 5px;
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 4px;
    transform: translateX(-50%);
    font-family: var(--hotspot-font);

    p {
      margin: 0;
      text-align: center;
      color: white;
      margin-top: 0;
      margin-top: 5px;
      margin-bottom: 5px;
      font-size: 0.9rem;
      line-height: 1rem;
      text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.7);
    }

    .m-type {
      p {
        text-transform: uppercase;
        color: rgb(9, 112, 197);
        font-style: italic;
      }
    }
  }

  &.hovering {
    animation: none;
    -webkit-animation: none;
    -moz-animation: none;

    .m-info {
      display: block;
    }
  }

  > .s-visited {
    position: absolute;
    width: $hsWidth * 0.6;
    height: $hsWidth * 0.6;
    bottom: 60%;
    left: 60%;
    z-index: 5;
  }
}

@-webkit-keyframes hpScaleBig {
  0% {
    width: $hsWidth;
    height: $hsWidth;
  }
  50% {
    width: $hsMaxWidth;
    height: $hsMaxWidth;
  }
  100% {
    width: $hsWidth;
    height: $hsWidth;
  }
}

@keyframes hpScaleBig {
  0% {
    width: $hsWidth;
    height: $hsWidth;
  }
  50% {
    width: $hsMaxWidth;
    height: $hsMaxWidth;
  }
  100% {
    width: $hsWidth;
    height: $hsWidth;
  }
}
